    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    height:100px;
    width: 300px;
    overflow: auto;
}
.high{
    background-color: lightgreen;
}
.middle{
    background-color: yellow;
}
.low{
    background-color: red;
