___CSS_LOADER_EXPORT___.push([module.id, `@font-face {
    font-family: 'Myfont';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    font-weight: bold;
    font-style: normal;
}
::selection{
    background: none;
}

:root{
    --maincolor: red;
    --subcolor: rgb(224, 224, 224);
    --hovermaincolor:  rgb(193,17,25);
}
html{
    font-family: 'Myfont';
    margin:0;
    background-color: black;
    color:white;
    height:100vh;
    letter-spacing: 0.2rem;
}
body{
    margin:0;
    height: 100%;
    display:flex;
    flex-direction: column;
}
header{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.homelogo{
    width:15rem;
}
.symbolMenu{
    display: flex;
    gap:1rem;
}
.symbolButton{
    background-color: var(--maincolor);
    border:none;
    border-radius: 10px;
}
.symbolButton:hover{
    cursor: pointer;
    background-color: var(--hovermaincolor);
}
.material-symbols-outlined{
    color:white;
    background-color: inherit;
    font-size: 2.5rem;
}
.material-symbols-outlined:hover{
    color:var(--subcolor);
}
main{
    display:flex;
    padding-bottom: 31px;
    margin-inline: 5vw;
    flex:1 0 0;
}
menu{
    width:150px;
    margin: 0;
    border-radius: 10px;
    padding:0;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap:2rem;
}
menu>*{
    font-size: 2rem;
    color:var(--subcolor);
}
menu>*:hover{
    color:var(--subcolor);
}
footer{
    position:fixed;
    bottom:0;
    width: 100%;
    text-align: center;
    color:gray;
    background-color: black;
    border-top:1px solid gray;
}
p{
    margin:0;
